
.App{
  width: 99vw;
  height: 99vh;



}

/*Navbar...*/

.btn-position{
  margin-left: 20%;
  width: 70%;
  margin-left: none;
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}



.grp-name{
  margin-top: 20px;
  font-style: Cursive;
  font-weight: 500;
  font-size: 20px;
}

/*styling  of the all avilable button....*/

.btn{
  height: 60%;
  margin-top: 1.1%;
  width: 8%;
  color: white;
  border: 3px solid grey;
  border-radius: 10px;
  background: rgba(255, 102, 0, 0.999);
}


/*styling of the hover button container.*/

#button-container{
  width: 23%;
  height: 15%;
  margin-left: 40%;
  margin-top: 3%;
}




.hover-btn{
  height: 100%;
  width: 100%;
  font-size: 2.5rem;

}




/*styling for the top position hover...*/

.top{
  height: 30%;
  width: 50%;
  margin-top: -55%;
  margin-left: 20%;
  padding: 6px 10px 15px 10px;
  border-radius: 10px;
  font-size: 1rem;
  color: white;
  background-color: black;
  border-bottom: 1px dotted black; 
  
}

.top::before{
  content: "";
  position: absolute;
  left: 50%;
  top: 23%;
  transform: translateX(-50%);
  border: 15px solid;
  border-color: #000 #0000 #0000 #0000;
}





/*styling for the right position hover...*/


.right{
    height: 30%;
    width: 50%;
    margin-top:-22%;
    margin-left: 103%;
    padding: 6px 10px 15px 10px;
    color: white;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: black;
}


.right::before{
  content: "";
  position: absolute;
  left: 62.5%;
  top: 305;
  transform: translateX(-50%);
  border: 15px solid;
  border-color: #0000 #000 #0000 #0000;
}



/*styling for the down position hover...*/


